import React from "react";
import { Link } from "react-router-dom";

const ProjectList = ({ projects }) => (
  <>
    {projects.map((project, key) => (
      <Link className="project-list-item" key={key} to={`/project/${project.name}`}>
        <div>
          <h3>{project.title}</h3>
          <p>{project.snippet}</p>
        </div>
      </Link>
    ))}
  </>
);

export default ProjectList;
