import React, { useState, useEffect } from "react";
import Hero from "../components/Hero";
import UpVoteSection from "../components/UpVote";
import projectContent from "../projects/project-content";
import ProjectList from "../components/ProjectList";
import NotFoundPage from "../pages/NotFoundPage";
import CommentsList from "../components/CommentsList";
import AddCommentForm from "../components/AddCommentForm";

const ProjectsSingle = ({ match }) => {
  const name = match.params.name;
  const project = projectContent.find(project => project.name === name);

  const [projectInfo, setProjectInfo] = useState({ upvotes: 0, comments: [] });
  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/project/${name}`);
      const body = await result.json();
      setProjectInfo(body);
    };
    fetchData();
  }, [name]);

  if (!project) return <NotFoundPage />;

  const otherProjects = projectContent.filter(project => project.name !== name);

  return (
    <>
      <Hero heading={project.title} subheading="" primary="/projects" primaryText="All Projects" secondary="/contact" secondaryText="Hire Me" />
      <div className="project-single-content" id="readContent">
        {project.content.map((paragraph, key) => (
          <p key={key}>{paragraph}</p>
        ))}
        <UpVoteSection projectName={name} upvotes={projectInfo.upvotes} setProjectInfo={setProjectInfo} />
        <CommentsList comments={projectInfo.comments} />
        <AddCommentForm projectName={name} setProjectInfo={setProjectInfo} />
        <br />
        <br />
        <hr />
        <h3>Other Articles</h3>
        <ProjectList projects={otherProjects} />
      </div>
    </>
  );
};

export default ProjectsSingle;
