import React, { useState } from "react";

const AddCommentForm = ({ projectName, setProjectInfo }) => {
  const [username, setUserName] = useState("");
  const [commentText, setCommentText] = useState("");

  const addComment = async () => {
    const result = await fetch(`/api/project/${projectName}/add-comment/`, {
      method: "post",
      body: JSON.stringify({ username, text: commentText }),
      headers: {
        "Content-Type": "application/json"
      }
    });
    const body = await result.json();
    setProjectInfo(body);
    setUserName("");
    setCommentText("");
  };

  return (
    <div id="add-comment-form">
      <h3>Add a Comment</h3>
      <label for="name input">
        Name:
        <input type="text" value={username} onChange={e => setUserName(e.target.value)} />
      </label>
      <label for="comment input">
        Comment:
        <textarea rows="4" cols="50" value={commentText} onChange={e => setCommentText(e.target.value)} />
      </label>
      <button onClick={() => addComment()} type="submit">
        Add Comment
      </button>
    </div>
  );
};

export default AddCommentForm;
