import React from "react";

const UpVoteSection = ({ projectName, upvotes, setProjectInfo }) => {
  const upVoteProject = async () => {
    const result = await fetch(`/api/project/${projectName}/upvote`, {
      method: "post"
    });
    const body = await result.json();
    setProjectInfo(body);
  };
  return (
    <>
      <div id="upvotes-section">
        <button onClick={() => upVoteProject()}>Yes</button>
        <p>Upvotes: {upvotes}</p>
      </div>
    </>
  );
};

export default UpVoteSection;
